body {
    --main-color1: #1a173e;
    --main-color2: #74b4ff;
    --main-color3: #0da2d8;
    --main-color4: #191735;
    --main-color5: #ff7575;
    --main-color6: #2dc7ff;
    --main-color7: #1a1836;
}

* {
    outline: none;
}

input {
    display: flex;
    align-items: center;
    height: 85px;
    color: #fff;
    background-color: var(--main-color4);
    border: 1px solid rgba(255, 255, 255, 0.19);
    box-sizing: border-box;
    border-radius: 16px;
    padding: 0px 25px;
    font-size: 22px;
    line-height: 28px;
    letter-spacing: 0.75px;

    &::placeholder {
        color: rgba(255, 255, 255, 0.21);
    }
}

button {
    box-shadow: none !important;
    border-radius: 12px !important;
    font-weight: bold !important;
    text-transform: none !important;
}
