.dashboard-home-page {
    display: flex;
    flex-direction: column;
    padding-top: 45px;

    .total-content {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-gap: 50px;

        .total-item {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            background: linear-gradient(199.31deg, #282458 -1.62%, #262353 60.4%, rgba(62, 59, 115, 0) 113.63%);
            filter: drop-shadow(0px 48px 69px rgba(23, 18, 43, 0.75));
            border-radius: 10px;
            height: 210px;

            .label {
                font-size: 18px;
                line-height: 127%;
                color: rgba(116, 180, 255, 0.7);
                margin-bottom: 18px;
            }

            .value {
                font-size: 65px;
                line-height: 127%;
                font-weight: bold;
                color: var(--main-color2);
            }
        }
    }

    .data-content {
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-top: 50px;
        padding: 35px 0px 60px 0px;
        background: linear-gradient(199.31deg, #282458 -1.62%, #262353 60.4%, rgba(62, 59, 115, 0) 113.63%);
        border-radius: 10px;
        box-sizing: border-box;

        .title-content {
            display: flex;
            align-items: center;
            padding: 0px 20px 0px 40px;
            margin-bottom: 25px;

            .title {
                font-size: 18px;
                line-height: 127%;
                color: rgb(116, 180, 255, 0.7);
                margin-right: 10px;
            }

            .count {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 49px;
                height: 32px;
                background-color: var(--main-color2);
                border-radius: 100px;
                font-size: 15px;
                line-height: 127%;
                font-weight: bold;
                color: #fff;
            }

            .btn-release {
                padding: 0px;
                color: var(--main-color2);
                font-size: 12px;
                margin-left: auto;
            }
        }

        .release-content {
            display: inline-flex;
            overflow-y: auto;
            overflow-x: hidden;
            transform-origin: right top;

            .release-item {
                min-width: 265px;
                margin-left: 40px;
            }
        }
    }
}
