.dashboard-licenses-page {
    display: flex;
    flex-direction: column;
    background: linear-gradient(199.31deg, #282458 -1.62%, #262353 60.4%, rgba(62, 59, 115, 0) 113.63%);
    filter: drop-shadow(0px 48px 69px rgba(23, 18, 43, 0.75));
    border-radius: 10px;

    table {
        border-spacing: 0px 12px;

        th {
            font-weight: bold;
            font-size: 16px;
            line-height: 127%;
            padding: 35px 0px;
        }

        th,
        td {
            text-align: left;
            color: #fff;

            &:first-child {
                padding-left: 40px;
            }
        }

        tbody tr {
            background-color: var(--main-color7);

            td {
                font-size: 16px;
                line-height: 127%;
            }
        }
    }
}
