.dashboard-page {
    display: flex;
    flex-direction: column;
    background-color: var(--main-color1);
    width: 100%;
    min-height: 100vh;
    padding: 0px 97px;
    box-sizing: border-box;

    .header-content {
        display: flex;
        align-items: center;
        padding: 30px 0px 20px;

        .header-title {
            position: relative;
            display: flex;
            align-items: center;
            z-index: 999;

            img {
                width: 82.5px;
                margin-right: 12px;
            }

            .title {
                font-weight: bold;
                font-size: 20px;
                line-height: 24px;
                color: #fff;
            }
        }

        .user-info {
            display: flex;
            align-items: center;
            margin-left: auto;

            .name {
                font-size: 24px;
                font-weight: bold;
                line-height: 29px;
                color: #fff;
                margin-right: 25px;
            }

            .avatar {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 90px;
                height: 90px;
                border-radius: 50%;
                background: linear-gradient(210.96deg, rgba(33, 29, 86, 0.87) 0.01%, #2a2763 42.05%, #2e2c5c 104.81%);
                border: 3px solid #99bcff;
                box-shadow: 0px 48px 69px rgba(23, 18, 43, 0.5);
                box-sizing: border-box;
                overflow: hidden;
                margin-right: 48px;

                img {
                    width: 82.5px;
                }
            }
        }
    }

    .page-tab-content {
        display: flex;
        border-bottom: 1px solid rgba(255, 255, 255, 0.16);
        padding-top: 38px;

        .tab-content {
            display: flex;
            padding-bottom: 24px;

            .tab-item {
                position: relative;
                font-size: 22px;
                line-height: 127%;
                font-weight: bold;
                color: #fff;
                cursor: pointer;

                &.active {
                    color: var(--main-color6);

                    &::before {
                        content: '';
                        position: absolute;
                        left: 0px;
                        bottom: -24px;
                        width: 100%;
                        border: 3px solid var(--main-color6);
                        border-radius: 7px;
                        box-sizing: border-box;
                    }
                }
            }

            .tab-item + .tab-item {
                margin-left: 124px;
            }
        }
    }

    .release-item {
        position: relative;
        height: 300px;
        display: flex;
        flex-direction: column;
        padding: 27px 20px 0px;
        background: linear-gradient(
            180deg,
            #1a1836 0%,
            rgba(26, 24, 54, 0.015625) 99.99%,
            rgba(26, 24, 54, 0) 100%
        );
        border: 1px solid rgba(255, 255, 255, 0.13);
        filter: drop-shadow(0px 48px 69px rgba(23, 18, 43, 0.75));
        border-radius: 10px;
        box-sizing: border-box;

        .item-img {
            width: 120px;
            height: 116px;
            margin: 0 auto;
        }

        .value {
            display: flex;
            align-items: flex-end;
            justify-content: flex-end;
            color: var(--main-color2);
            margin-top: 17px;
            margin-bottom: 7px;

            .main-value {
                font-size: 15px;
                font-weight: bold;
            }

            .total-value {
                font-size: 10px;
                color: rgba(116, 180, 255, 0.7);
            }
        }

        .label {
            font-weight: bold;
            font-weight: 18px;
            line-height: 127%;
            color: #fff;
            margin-top: 19px;
        }

        .date {
            font-size: 15px;
            line-height: 127%;
            color: rgba(255, 255, 255, 0.5);
            margin-top: 12px;
        }

        .btn-detail {
            position: absolute;
            top: 12px;
            right: 12px;
            padding: 0px;
            min-width: 0px;
        }
    }

    .bottom {
        font-size: 18px;
        line-height: 127%;
        color: rgba(255, 255, 255, 0.5);
        margin: 55px auto 45px;
    }
}
